body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 650px;
  height: 100vh;
  padding-left: 10px;
  margin: 50px auto;
  background-color: #f7d322;
  border-radius: 10px;
  box-shadow: none;
  font-family: 'Gochi Hand', cursive;
}

.App-header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 80%;
  height: auto;
  color: black;
}

.loading-image {
    border-radius: 10px;
}


/* Portrait and Landscape */
@media only screen
  and (min-device-width: 375px)
  and (max-device-width: 800px)
  and (-webkit-min-device-pixel-ratio: 2) {

    .App {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      width: 100vw;
      height: 100vh;
      padding-left: 10px;
      margin: none;
      background-color: #f7d322;
      border-radius: none;
      font-family: 'Gochi Hand', cursive;
      box-shadow: none;
    }

    .App-header {
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column;
      -webkit-align-items: center;
              align-items: center;
      -webkit-justify-content: center;
              justify-content: center;
      width: 90%;
      height: auto;
    }

}

.quoteBox {
  width: 100%;
  height: 400px;
  background-color: #f7d322;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
}

.charImage {
  /* border: 2px solid black; */
  background-color: #f7d322;
  width: 45%;
  height: auto;
  max-height: 90%;
  margin-left: 3%;
  margin-right: 1%;
}

.textBox {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  /* border: 2px solid black; */
  /* background-color: white; */
  width: 45%;
  height: auto;
  max-height: 90%;
  margin-left: 2%;
  margin-right: 3%;
}

.charQuote {
  height: auto;
  width: 90%;
  max-width: 95%;
  border: 1.5px solid black;
  background-color: white;
  border-radius: 15px;
  padding: 5%
}

.charName {
  height: 10%;
  width: 90%;
  border: 2px solid black;
  background-color: #f7d322;
  border: none;
  background-color: none;
}

